@import 'ScssHelpers/helpers-all.scss';

.extracted {
	visibility: visible;
	z-index: 2;

	@include bp($point_3) {
		padding-top: 8.6rem;
	}
}

.extracted__cols {
	display: flex;
	align-items: center;
	flex-wrap: wrap;

	@include bp($point_2, min) {
		margin: 0 -3rem 0 0;
	}
}

.extracted__col {
	@include bp($point_2, min) {
		width: 50%;
		padding: 0 3rem 0 0;
	}

	@include bp($point_3) {
		width: 100%;

		&:nth-child(1) {
			order: 2;
		}

		&:nth-child(2) {
			margin-bottom: 3.8rem;
		}
	}
}

.extracted_table {
	position: relative;

	max-width: 100%;

	background-color: #fafaf7;
	border-radius: 0.4rem;

	@include bp($point_2, min) {
		padding: 2.6rem 5.6rem 4.6rem;
		width: 54.2rem;

		border: 0.4rem solid $black;
	}

	@include bp($point_3) {
		width: 100%;
		padding: 2.8rem 2.4rem 2.6rem;

		border: 0.3rem solid $black;
	}

	&:after {
		content: '';
		position: absolute;

		background-color: $black;
		border-radius: 0.4rem;
		opacity: 0.9;
		filter: blur(0.4rem);

		z-index: -1;
		pointer-events: none;

		will-change: filter;

		@include bp($point_2, min) {
			left: 1rem;
			top: 2rem;

			width: calc(100% + 0.8rem);
			height: 100%;
		}

		@include bp($point_3) {
			left: 0.8rem;
			top: 0.4rem;

			width: calc(100% + 0.4rem);
			height: calc(100% + 0.6rem);
		}
	}
}

.extracted_table__title {
	margin-bottom: 0.8rem;

	text-align: center;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 3.34rem;
	}
}

.extracted_table__tabs {
	display: flex;

	@include bp($point_2, min) {
		margin-bottom: 1.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 1rem;
	}
}

.extracted_table__tabs_item {
	width: 50%;

	font-weight: 700;
	font-family: $raleway;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_2, min) {
		padding-bottom: 0.6rem;

		font-size: 0.63rem;
		color: #565656;
		border-bottom: 2px solid rgba(#bbb, 0.4);
	}

	@include bp($point_3) {
		padding-bottom: 1.2rem;

		font-size: 1.17rem;
		color: #808080;
		border-bottom: 3px solid rgba(#bbb, 0.4);
	}

	&.extracted_table__tabs_item--active_mod {
		color: $black;
		border-color: $black;
	}
}

// head

.extracted_table__head {
	display: flex;

	@include bp($point_2, min) {
		margin-bottom: 1.6rem;
	}

	@include bp($point_3) {
		margin-bottom: 1rem;
	}
}

.extracted_table__search {
	position: relative;

	width: 11.2rem;

	// pointer-events: none;

	@include bp($point_3) {
		display: none;
	}
}

.extracted_table__search_icon {
	position: absolute;
	top: 50%;
	left: 1rem;

	width: 1.1rem;
	height: 1.1rem;
	margin-top: -0.55rem;
}

.extracted_table__head_buttons {
	margin-left: auto;
	display: flex;
}

.extracted_table__head_buttons_item {
	&:not(:last-child) {
		margin-right: 0.2rem;
	}

	&:last-child {
		@include bp($point_3) {
			display: none;
		}
	}
}

.extracted_table__head_button {
	border-radius: 50%;

	@include bp($point_2, min) {
		width: 1.8rem;
		height: 1.8rem;
		padding: 0.4rem;
	}

	@include bp($point_3) {
		width: 3.4rem;
		height: 3.4rem;
		padding: 0 0.6rem;
	}

	&.extracted_table__head_button--v2_mod {
		background-color: $yellow;
	}
}

// list

.extracted_table__list {
	display: flex;
	flex-wrap: wrap;

	@include bp($point_2, min) {
		margin: 0 -1rem -1rem 0;
	}
}

.extracted_table__item {
	position: relative;

	@include bp($point_2, min) {
		width: 50%;
		padding: 0 1rem 1rem 0;
	}

	@include bp($point_3) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}

		&:nth-child(n + 3) {
			display: none;
		}

		&:nth-child(4) {
			display: block;
		}
	}
}

.extracted_table__item_in {
	position: relative;

	border-bottom: 2px solid $black;

	@include bp($point_2, min) {
		padding: 0 0.4rem;
	}

	@include bp($point_3) {
		padding: 0 0 0.4rem;
	}
}

.extracted_table__unverified_wrap {
	position: absolute;
	bottom: 0;
	left: 50%;

	transition: opacity 0.3s;

	@include bp($point_2, min) {
		width: 2.4rem;
		height: 2.4rem;
		margin: 0 0 0.4rem 2.4rem;
	}

	@include bp($point_3) {
		width: 4.6rem;
		height: 4.6rem;
		margin: 0 0 3.4rem 8.4rem;
	}

	.extracted_table__item--verified_mod & {
		opacity: 0;
		pointer-events: none;
	}

	.extracted_table__preview & {
		width: 6rem;
		height: 6rem;
		margin: 0 0 4.4rem 5.4rem;

		transition: filter 0.3s;
		cursor: pointer;
	}
}

.extracted_table__unverified {
	display: block;
	width: 100%;
	height: 100%;

	will-change: transform, opacity;
}

.extracted_table__item_unverified_img {
	@include contain_img;
}

.extracted_table__item_head {
	display: flex;
	align-items: center;

	@include bp($point_2, min) {
		margin-bottom: 0.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 0.4rem;
	}

	.extracted_table__preview & {
		margin-bottom: 0.4rem;
	}
}

.extracted_table__item_logo {
	margin-right: 0.6rem;
	flex-shrink: 0;

	@include bp($point_2, min) {
		height: 1.4rem;
		max-width: 2.2rem;
		margin-top: -0.3rem;
	}

	@include bp($point_3) {
		height: 2.2rem;
		max-width: 4rem;
		margin-top: -1rem;
	}

	.extracted_table__preview & {
		margin-right: 0.4rem;
		max-width: 5rem;
		height: 2rem;
	}
}

.extracted_table__item_logo_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.extracted_table__item_title {
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		font-size: 1.84rem;
	}

	.extracted_table__preview & {
		font-size: 3.2rem;
		line-height: 1;
	}

	&:not(:last-child) {
		margin-right: 0.7rem;
	}
}

.extracted_table__item_verified {
	opacity: 0;
	transform: translateY(1rem);

	will-change: transform, opacity;

	@include bp($point_2, min) {
		width: 1rem;
		height: 1rem;
	}

	@include bp($point_3) {
		width: 2rem;
		height: 2rem;
	}

	.extracted_table__preview & {
		width: 2rem;
		height: 2rem;
	}

	.extracted_table__item--verified_mod & {
		opacity: 1 !important;
		transform: translateY(0) !important;
	}
}

.extracted_table__item_verified_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.extracted_table__subtitle {
	margin-bottom: 0.2rem;

	font-weight: 500;
	font-family: $raleway;
	font-style: italic;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 0.62rem;
	}

	@include bp($point_3) {
		font-size: 1.17rem;
	}

	.extracted_table__preview & {
		font-size: 1.4rem;
	}
}

.extracted_table__category {
	font-weight: 700;
	font-family: $raleway;
	text-transform: uppercase;

	@include bp($point_2, min) {
		margin-bottom: 0.7rem;

		font-size: 0.62rem;
	}

	@include bp($point_3) {
		margin-bottom: 4.2rem;

		font-size: 1.17rem;
	}

	.extracted_table__preview & {
		margin-bottom: 1.8rem;
		font-size: 1.4rem;
	}
}

.extracted_table__footer {
	display: flex;
	align-items: center;
	padding-bottom: 0.3rem;

	.extracted_table__preview & {
		border-bottom: 0.2rem solid $black;
		margin: 0 -1rem;
		padding: 0 1rem 0.8rem;
	}
}

.extracted_table__date {
	font-family: $dmsans;
	font-weight: 700;
	font-style: italic;

	@include bp($point_2, min) {
		font-size: 0.71rem;
	}

	@include bp($point_3) {
		margin-right: 1.4rem;

		font-size: 1.33rem;
	}

	.extracted_table__preview & {
		font-size: 1.6rem;
	}
}

.extracted_table__users {
	display: flex;
	margin-top: -0.8rem;

	@include bp($point_2, min) {
		margin-left: auto;
	}

	@include bp($point_3) {
		margin: 0 1rem 0 0;

		.extracted_table__preview & {
			margin: 0 0 0 auto;
		}
	}
}

.extracted_table__users_item {
	position: relative;
	z-index: 2;

	font-size: 0.534rem;
	font-weight: 700;
	font-family: $raleway;
	color: $white;

	// background-color: #555555;
	border-radius: 50%;
	// overflow: hidden;

	@include bp($point_2, min) {
		width: 1.1rem;
		height: 1.1rem;
	}

	@include bp($point_3) {
		width: 2rem;
		height: 2rem;
	}

	.extracted_table__preview & {
		width: 2.2rem;
		height: 2.2rem;

		font-size: 1.2rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: -0.45rem;
		}

		@include bp($point_3) {
			margin-right: -0.8rem;
		}

		.extracted_table__preview & {
			margin-right: -0.8rem;
		}
	}
}

.extracted_table__users_pic {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;

	display: block;
	width: 100%;
	height: 100%;

	border-radius: 50%;
	overflow: hidden;

	opacity: 0;

	will-change: opacity;

	.extracted_table__item--verified_mod & {
		opacity: 1;
	}
}

.extracted_table__users_img {
	@include cover_img;
}

.extracted_table__users_text {
	position: absolute;
	top: -2px;
	left: -2px;
	right: -2px;
	bottom: -2px;

	display: flex;
	align-items: center;
	justify-content: center;
	padding-top: 0.1rem;

	font-family: $raleway;
	font-weight: 600;

	border: 2px solid $white;
	border-radius: 50%;
	background-color: #555;

	.extracted_table__preview .extracted_table__users_item:nth-child(n + 4):last-child & {
		padding-bottom: 0.3rem;
	}

	@include bp($point_3) {
		font-size: 1rem;

		.extracted_table__users_item:nth-child(n + 4):last-child & {
			padding-bottom: 0.3rem;
		}
	}
}

.extracted_table__footer_icons {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: flex;
		margin-left: auto;
		margin-right: -0.3rem;
	}
}

.extracted_table__footer_icons_item {
	width: 2.2rem;
	height: 2.2rem;

	&:not(:last-child) {
		margin-right: 0.6rem;
	}
}

.extracted_table__btn {
	width: 100%;

	@include bp($point_2, min) {
		display: none;
	}
}

// preview

.extracted_table__preview {
	position: absolute;
	top: 1.8rem;
	z-index: 3;

	border: 0.4rem solid $black;
	border-radius: 0.4rem;

	opacity: 0;
	pointer-events: none;

	transform: translateY(-3rem) scale(0.4);
	transform-origin: 50% 0%;

	will-change: transform, opacity;

	@include bp($point_2, min) {
		left: -54%;

		width: 245%;
		padding: 2.6rem 3.8rem 2.6rem;
	}

	@include bp($point_3) {
		left: -5rem;

		width: calc(100% + 10rem);
		padding: 2.6rem 2rem;
	}

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #fafaf7;

		pointer-events: none;
		will-change: filter;

		z-index: -1;
	}

	&:after {
		content: '';
		position: absolute;
		left: 1rem;
		top: 2rem;

		width: calc(100% + 0.8rem);
		height: 100%;

		background-color: $black;
		border-radius: 0.4rem;
		opacity: 0.9;
		filter: blur(0.4rem);

		z-index: -2;
	}

	.extracted_table__item--active_mod & {
		opacity: 1;
		pointer-events: initial;

		transform: translateY(0) scale(1);
	}
}

.extracted_table__cursor {
	position: absolute;
	top: 60%;
	left: 60%;

	width: 2.6rem;
	height: 2.6rem;

	z-index: 3;

	will-change: transform, opacity;
}

.extracted_table__cursor_pic {
	display: block;
	width: 100%;
	height: 100%;
}

.extracted_table__cursor_img {
	@include contain_img;
}

// title

.extracted__title_block {
	position: relative;

	@include bp($point_2, min) {
		max-width: 45.6rem;
		margin-left: auto;
	}

	&:before {
		content: '';
		position: absolute;

		width: 1.5rem;
		height: 1.5rem;

		border-radius: 50%;
		background-color: $black;

		@include bp($point_2, min) {
			top: -2.8rem;
			left: -2.2rem;
		}

		@include bp($point_3) {
			top: -3.4rem;
			left: -1rem;
		}
	}
}

.extracted__title_wrap {
	position: relative;

	border-top: 0.4rem solid $black;
	border-bottom: 0.4rem solid $black;

	overflow: hidden;
}

.extracted__title {
	padding: 1.2rem 0;
	min-height: 100%;

	will-change: opacity;

	* {
		font-weight: 400;
	}

	@include bp($point_2, min) {
		min-height: 14.6rem;
	}

	@include bp($point_4 - 1) {
		min-height: 19.2rem;
	}

	&:not(:first-child) {
		position: absolute;
		top: 100%;
		left: 0;

		pointer-events: none;
	}
}

.extracted__title_in {
	@include h4;

	strong,
	span {
		font-weight: 400;
		color: $yellow;
	}
}
